import React from "react";
import { Navigation, Pagination } from "swiper";
import { Swiper, SwiperSlide } from "swiper/react/swiper-react.js";

const ModalSlider = ({ usersArr, userIndex, swiperId }) => {
  return (
    <>
      <Swiper
        id={swiperId}
        modules={[Navigation, Pagination]}
        spaceBetween={50}
        slidesPerView={1}
        navigation
        initialSlide={userIndex}
        pagination={{ clickable: true }}
        className="mainPersonCard__photoContainer"
        watchOverflow
        slidesPerView={1}
        slidesPerGroup={1}
        tag="section"
        wrapperTag="ul"
      >
        {usersArr.map((info) => {
          return (
            <SwiperSlide
              key={info.id}
              tag="li"
              className="mainPersonCard__modalPhotoContainer"
            >
              <div className="mainPersonCard__modalInfo">
                <div className="mainPersonCard__modalPhoto">
                  <img src={info.photoXL} alt={info.name} />
                </div>

                <div className="mainPersonCard__name">{info.name}</div>
                <div className="mainPersonCard__position">{info.position}</div>
                <div className="mainPersonCard__depart">{info.depart}</div>
                {info.birthDay && (
                  <>
                    <div className="mainPersonCard__birthDay">
                      {`${info.birthDay}.${
                        info.birthMonth < 10
                          ? `0${info.birthMonth}`
                          : info.birthMonth
                      }`}
                    </div>
                  </>
                )}
              </div>
            </SwiperSlide>
          );
        })}
      </Swiper>
    </>
  );
};

export default ModalSlider;
