import React, { useState } from "react";
import { Modal, Button } from "antd";

import { ModalSlider } from '../';

const MainUserCard = ({ userIndex, imageS, name, pos, dep, day, month, usersArr, swiperId }) => {
  const [visible, setVisible] = useState(false);
  // console.log('usersArr', usersArr);

  return (
    <>
      <div className="mainPersonCard">
        <div className="mainPersonCard__photo">
          <Button
            onClick={() => setVisible(true)}
            className="mainPersonCard__switchModal"
          >
            <img src={imageS} alt={name} />
          </Button>
        </div>

        <div className="mainPersonCard__descr">
          <div className="mainPersonCard__personInfo">
            <div className="mainPersonCard__name">{name}</div>
            <div className="mainPersonCard__position">{pos}</div>
            <div className="mainPersonCard__depart">{dep}</div>

            {day && (
              <>
                <div className="mainPersonCard__birthDay">
                  {`${day}.${month < 10 ? `0${month}` : month}`}
                </div>
              </>
            )}
          </div>
        </div>
      </div>

      <Modal
        title={" "}
        centered
        visible={visible}
        onOk={() => setVisible(false)}
        onCancel={() => setVisible(false)}
        width={700}
        footer={null}
      >
        <ModalSlider
          usersArr={usersArr}
          userIndex={userIndex}
          swiperId={swiperId}
        />
      </Modal>
    </>
  );
};

export default MainUserCard;
